import React, {useState, useEffect} from "react";
import axios from "axios";
import NavBar from "./NavBar";


const SendInvitesPage = ({ onSendInvite, leagueId, isLoggedIn }) => {
    const [query, setQuery] = useState('');
    const [results, setResults] = useState([]);
    const [errorMessage, setErrorMessage] = useState('');
    const [searchClicked, setSearchClicked] = useState(false)
  
    const handleSearch = async () => {
      try {
        const response = await axios.get(`http://localhost:8000/api/searchUsers?email=${query}`);
        setResults(response.data);
        setErrorMessage('');
        setSearchClicked(true); // Set searchClicked to true when search button is clicked

      } catch (error) {
        console.error('Error searching for users:', error);
        setErrorMessage('Error searching for users. Please try again.');
      }
    };
  
    const handleSendInvite = (userId) => {
      // Assuming onSendInvite is a function that sends an invite
      onSendInvite(userId, leagueId);
    };
  
    return (
      <div className="SendInvitesPage">
        <input type="text" value={query} onChange={(e) => setQuery(e.target.value)} />
        <button onClick={handleSearch}>Search</button>
  
        {errorMessage && <p>{errorMessage}</p>}
  
        {searchClicked && results.length > 0 ? (
        <ul>
          {results.map((user) => (
            <li key={user._id}>
              {user.name} - {user.email}
              <button onClick={() => handleSendInvite(user._id)}>Send Invite</button>
            </li>
          ))}
        </ul>
      ) : searchClicked ? (
        <p>Could not find user with email {query}</p>
      ) : null}
      </div>
    );
  };

export default SendInvitesPage;

