import React, { useEffect, useState }from "react";
import axios from "axios";
import './styles/TeamCard.css';


export default function TeamCard({teamName, teamId, teamOwner}) {
    const [projectedPoints, setProjectedPoints] = useState(0);
    const [ownerName, setOwnerName] = useState('');
    
    useEffect(() => {
        axios.get(`http://localhost:8000/api/getTeamProjectedPoints/${teamId}`)
            .then((response) => {
                setProjectedPoints(response.data);
            })
            .catch((error) => {
                console.error(error);
            })
    })


    useEffect(() => {
        axios.get(`http://localhost:8000/api/getTeamOwnerName/${teamOwner}`)
            .then((response) => {
                setOwnerName(response.data);
            })
            .catch((error) => {
                console.error(error);
            })
    })


    return (
        <div className="teamCard" >
            <h1> {teamName} </h1>
            <h1> Projected Points: {projectedPoints.toFixed(2)} </h1>
            <h1> Team Owner: {ownerName} </h1>
        </div>
    )
}