import React, { useEffect, useState } from "react";
import './styles/MyLeaguesPage.css'
import service from "../API/service";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import PageHeader from "./PageHeader";
import TeamCard from "./TeamCard";
import SendInvitesPage from "./SendInvitesPage";


async function fetchTeamData(teamId) {
    try {
      const response = await axios.get(`http://localhost:8000/api/getFantasyTeamById/${teamId}`);
      console.log(JSON.stringify(response.data, null, 2))

      return response.data;
    } catch (error) {
      console.error(`Error fetching data for team ${teamId}: ${error}`);
      return null;
    }
  }  


export default function LeaguePage(props) {
    const params = useParams();
    const id = params.id 
    const [leagueData, setLeagueData] = useState({});
    const [teams, setTeams] = useState([]);
    const [teamData, setTeamData] = useState({});
    const [showSendInvites, setShowSendInvites] = useState(false);
    const [schedule, setSchedule] = useState([]);



    useEffect(() => {
        axios.get(`http://localhost:8000/api/getFantasyLeagueById/${id}`)
            .then((response) => {
                setLeagueData(response.data);
                setTeams(response.data.teams);
            })
            .catch((error) => {
                error.log(error);
            })
    }, [id]);

    useEffect(() => {
        const fetchTeamDataForTeams = async () => {
          const teamDataPromises = teams.map((teamId) => fetchTeamData(teamId));
          const teamDataArray = await Promise.all(teamDataPromises);
          const teamDataMap = {};
    
          teamDataArray.forEach((data, index) => {
            if (data) {
              teamDataMap[teams[index]] = data;
            }
          });
    
          setTeamData(teamDataMap);
        };
    
        fetchTeamDataForTeams();
      }, [teams]);


    const fetchSchedule = () => {
      const body = {
        "teams": teams
      };

      console.log("schedule in body:", JSON.stringify(body, null, 2));
      axios.post(`http://localhost:8000/api/getLeagueSchedule`, body)
        .then((response) => {
          console.log(response.data);
          setSchedule(response.data);
        })
        .catch((error) => {
          console.error(error);
        })
    };


    const joinLeague = () => {
      const body = {
        id
      }

      axios.post(`http://localhost:8000/api/joinLeague/${id}`, null, { withCredentials: true })
        .then((response) => {
          console.log(JSON.stringify(response.data, 2, null))
        })
        .catch((error) => {
          console.error(error);
        })
    }


    const sendInvite = (userId, leagueId) => {
      const body = {
        userId,
        leagueId
      }

      axios.post(`http://localhost:8000/api/createInvite`, body)
        .then((response) => {
          console.log(response);
          setShowSendInvites(false);
        })
        .catch((error) => {
          console.error(error);
        })
    }
    


    return (
        <div className="LeaguePage">
            <PageHeader title={leagueData.leagueName} isLoggedIn={props.isLoggedIn}/>
            <h1> Welcome To {leagueData.leagueName}! </h1>
            <h1> Members: {leagueData.leagueMembers}</h1>
            <button onClick={() => setShowSendInvites(!showSendInvites)} > Invite User </button>
            {showSendInvites && <SendInvitesPage onSendInvite={sendInvite} leagueId={id}/>} 

            <h2>Teams:</h2>
            <ul>
                {teams.map((teamId, index) => (
                <li key={index} className="team-list-item">
                    {teamData[teamId] ? (
                   <Link to={`/team/${teamId}`} state={{ id }}>
                        <TeamCard teamName={teamData[teamId].teamName} teamId={teamId} teamOwner={teamData[teamId].ownerId}/>
                    </Link>
                    ) : (
                    `Data not available for team ${teamId}`
                    )}
                </li>
                ))}
            </ul>

            <button onClick={() => joinLeague()}> Join League </button>

            <button onClick={() => fetchSchedule()}> Get Schedule </button>
            <h2>Schedule:</h2>
            <ul>
              {schedule.map((round, roundIndex) => (
                <div key={roundIndex}>
                  <h3>Round {roundIndex + 1}</h3>
                  <ul>
                    {round.map((match, matchIndex) => (
                      <li key={matchIndex}>
                        {match.map((teamId, teamIndex) => (
                          <span key={teamIndex}>
                            {teamData[teamId] ? (
                              <Link to={`/team/${teamId}`}>
                                {teamData[teamId].teamName}
                              </Link>
                            ) : (
                              `Team ${teamId}`
                            )}
                            {teamIndex === 0 && " vs. "}
                          </span>
                        ))}
                      </li>
                    ))}
                  </ul>
                </div>
              ))}
          </ul>
        </div>
    )
}


